import { create } from "zustand";

export interface SubscriptionSelectionStore {
  subscriptionSelection: "monthly" | "yearly";
  setSubscriptionSelection: (selection: "monthly" | "yearly") => void;
}

export const useSubscriptionSelectionStore = create<SubscriptionSelectionStore>(
  (set) => ({
    subscriptionSelection: "monthly",
    setSubscriptionSelection: (selection: "monthly" | "yearly") =>
      set(() => ({ subscriptionSelection: selection })),
  }),
);
