"use client";

import { useSubscriptionSelectionStore } from "@/store/subscriptionSelection";

const SubscriptionSelection = () => {
  const subscriptionSelection = useSubscriptionSelectionStore(
    (state) => state.subscriptionSelection,
  );
  const setSubscriptionSelection = useSubscriptionSelectionStore(
    (state) => state.setSubscriptionSelection,
  );

  return (
    <div className="relative flex h-13 w-74 items-center justify-between rounded-full bg-black/10 px-2 py-2 shadow-lg ring-1 ring-white/10 backdrop-blur-2xl">
      <button
        onClick={() => setSubscriptionSelection("monthly")}
        className={`absolute left-2 h-9 cursor-pointer rounded-full px-4 py-1 font-medium text-[oklch(0.95_0_0)] transition-all duration-300 ease-in-out hover:ring-1 hover:ring-indigo-500 ${subscriptionSelection === "monthly" ? "bg-indigo-700/40 ring-1 ring-indigo-500" : ""}`}
      >
        Monthly
      </button>
      <button
        onClick={() => setSubscriptionSelection("yearly")}
        className={`right absolute right-2 flex h-9 w-45 cursor-pointer items-center justify-between rounded-full px-0.5 py-1 transition-all duration-300 ease-in-out hover:ring-1 hover:ring-indigo-500 ${subscriptionSelection === "yearly" ? "bg-indigo-700/40 ring-1 ring-indigo-500" : ""} `}
      >
        <span className="rounded-full pl-4 font-medium text-[oklch(0.95_0_0)]">
          Yearly
        </span>
        <span className="rounded-full bg-indigo-700/40 px-4 py-1 font-medium text-[oklch(0.95_0_0)] shadow-lg backdrop-blur-2xl">
          Save 20%
        </span>
      </button>
    </div>
  );
};

export default SubscriptionSelection;
